import styled from "styled-components"

export const StyledGrid = styled.div`
    background-color: #50509c;
    border-radius: 30px;
    box-shadow: -10px -10px 10px #474746,
                10px 10px 10px #dff2b4;
    display: flex;
    margin: 1vw 5vw;
    padding: 1vw;
    transform: rotateX(0.5turn);
`